<template>
  <div class="wrapper" :class="flag===1?'wrapper-bg':'wrapper-bg2'">
    <ul class="btnUl">
      <li
        v-for="(item, index) in routeWrapperParam"
        :key="index"
        class="btnLi"
      >
        <router-link
          :to="item.link"
          tag="span"
          active-class="active"
        >{{item.eachName}}
        </router-link>
      </li>
    </ul>

    <transition>
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
  export default {
    name: "lx",
    data() {
      return {
        routeWrapperParam: [
          {link: "/jghx/xfbm/xfbmaqh", eachName: "按区划"},
          {link: "/jghx/xfbm/xfbmabm", eachName: "按部门"}
        ]
      }
    },
    computed: {
      flag() {
        let flag = '';
        if (this.$route.path === '/jghx/xfbm/xfbmaqh') {
          flag = 1;
        } else if (this.$route.path === '/jghx/xfbm/xfbmabm') {
          flag = 2;
        }
        return flag
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .wrapper-bg
    background url("../../../assets/wrapper-bg.png") no-repeat center
    background-size 100% 100%

  .wrapper-bg2
    background url("../../../assets/wrapper-bg3.png") no-repeat center
    background-size 100% 100%

  .wrapper
    position relative
    height 100%
    .btnUl
      clear both
      position absolute
      width 7.4rem
      height 0.5rem
      top 0.4rem
      left 5.4rem
      z-index 1000

      .btnLi
        float left
        color #8df6ff
        line-height 0.5rem
        font-size 0.2rem
        margin 0 0.2rem
        cursor pointer

      .btnLi .active
        color #fff
        font-weight 700
        text-shadow 0 0 5px #0cafc9
    .v-enter, v-leave
      opacity 0
    .v-enter-active
      transition opacity 1s
    .v-enter-to
      opacity 1
</style>
